import { Component, HostBinding, OnInit } from '@angular/core';
import { DeliveryService } from '@jmart-cabinet/modules/settings/services/delivery.service';

@Component({
  selector: 'j-tariff',
  templateUrl: './tariff.component.html',
  styleUrls: ['./tariff.component.scss']
})
export class TariffComponent implements OnInit {
  public tariffs;
  public type = 'my';
  public loading = false;
  readonly monthGroup = [3, 6, 9, 12, 18, 24];
  @HostBinding('style.--cellLength')
  private cellLength: number;
  constructor(private deliveryService: DeliveryService) { }

  ngOnInit(): void {
   this.getTariff(this.type);
  }
  getTariff(type){
    this.loading = true;
    this.deliveryService.getTariff(type).subscribe(data => {
      console.log(data);
      this.tariffs = data;
      this.cellLength = data.cellLength;
      this.loading = false;
    });
  }

  public typeSelect(type: string) {
    this.type = type;
    this.getTariff(type);
  }
}
